.location {
  position: relative;
  height: 365px;
  width: 100%;
  max-width: 450px;
  padding:26px ;     
  border-radius: 10px;
  border:2px solid #F8D24E;
  background-color: var(--color-white-main);
  @media screen and (min-width: 768px) {
      width: 568px;
      max-width: 568px;
      height: 458px;
      padding:30px 58px ;    
    }

}


.location__subtitle{
  font-family: Seymour One;   
  font-size: 16px;
  line-height: 1.5;
  letter-spacing: normal;
  color: var(--color-black-main);
  text-align: center;
  margin-bottom: 20px;
  @media screen and (min-width: 768px) {
      margin-bottom: 32px;  
      font-size: 20px;
      line-height: 1.4; 
    }
}
.location_map{
  margin-bottom: 24px;
  width:100%;
  height: 224px;
  @media screen and (min-width: 768px) {
      margin-bottom: 32px;   
    }
}
.map--google{
  width:100%;
  height: 100%;
}
.location__button-send{
  font-size: 12px;
   line-height: 1.25;
   display: block;   
  margin: 0 auto;
  @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.21;  
    }

}
