:root {
  --color-white-main: #fcfcfc;
  --color-yellow-main: #f8d24e;
  --color-yellow-secondary: #efd478;
  --color-green-main: #84a178;
  --color-green-secondary: #aec6a5;
  --color-brown-main: #d6936d;
  --color-grey-main: #ababab;
  --color-grey-secondary: #7a7a7a;
  --color-black-main: #131313;
  --color-green-button: #0a8817;
  --color-gray-radio: #d9d9d9;
  --default-font: "Montserrat", sans-serif;
  --secondary-font: "Seymour One", sans-serif;
  --lt-sp-main: .04em;
}

.modal-window__card, .swiper-pagination-bullet, .mobile-menu__button, .nav__list-mobile, .button, .link {
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

link {
  color: currentColor;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
}

html {
  scroll-behavior: smooth;
}

body {
  font-size: 12px;
  font-family: var(--default-font);
  letter-spacing: .02em;
  color: var(--color-white-main);
}

@media screen and (min-width: 1200px) {
  body {
    font-size: 16px;
  }
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.section {
  min-width: 320px;
  background-color: var(--color-green-main);
}

.container {
  min-width: 320px;
  max-width: 448px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.button.buy__button, .button.location__button, .button.read-more__button {
  background-color: var(--color-yellow-main);
}

.button.buy__button:hover, .button.location__button:hover, .button.read-more__button:hover {
  background-color: var(--color-green-button);
}

.button.buy__button:focus, .button.location__button:focus, .button.read-more__button:focus {
  color: var(--color-white-main);
  background-color: var(--color-green-button);
}

.button.buy__button:disabled, .button.location__button:disabled, .button.read-more__button:disabled {
  background-color: var(--color-grey-main);
}

.button.franchise__button, .link.products__link {
  border: 2px solid var(--color-yellow-main);
  color: var(--color-black-main);
  background-color: rgba(0, 0, 0, 0);
}

.button.franchise__button:hover, .link.products__link:hover {
  border-color: var(--color-green-button);
}

.button.franchise__button:focus, .link.products__link:focus {
  color: var(--color-white-main);
  border-color: var(--color-green-button);
}

.button.franchise__button:disabled, .link.products__link:disabled {
  border-color: var(--color-grey-main);
}

.link {
  transition-property: color;
}

.link.products__link {
  color: var(--color-black-main);
  border-radius: 8px;
  padding: 4px 11px;
  font-size: 8px;
  font-weight: 600;
  text-decoration: none;
}

@media screen and (min-width: 1200px) {
  .link.products__link {
    border-radius: 10px;
    padding: 12px 34px;
    font-size: 14px;
    font-weight: 600;
    line-height: 1.28571;
  }
}

.button {
  font-family: var(--default-font);
  color: var(--color-black-main);
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 12px 34px;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.28571;
  transition-property: background-color, border, color;
}

.button.location__button {
