}

.arrow-one {
  stroke: currentColor;
  fill: none;
  margin-left: 2px;
  position: relative;
  top: 1px;
}

.arrow-two {
  stroke: currentColor;
  width: 42px;
  height: 15px;
  color: var(--color-black-main);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.arrow-two:hover {
  transform-origin: 0;
  cursor: pointer;
  transform: scaleX(1.25);
}

.arrow-two:focus {
  transform-origin: 0;
  cursor: pointer;
  transform: scale(1.25);
}

.modal-button-close {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition-property: transform, color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 9px;
  right: 9px;
}

.modal-button-close:hover, .modal-button-close:focus {
  background-color: var(--color-yellow-main);
  transform: scale(1.2);
}

