.modal-window-ingridients {
  max-height: 95vh;
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-width: thin;
  scrollbar-color: var(--color-yellow-main) transparent;
  padding-top: 24px;
  padding-bottom: 32px;
  padding-right: 20px;
  padding-left: 20px;
}

.modal-ingr {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  border: 3px solid #f8d24e;
  background: white;

  @media screen and (min-width: 768px) {
    width: 584px;
  }
  ::-webkit-scrollbar {
    width: 5px;
  }

  ::-webkit-scrollbar-thumb {
    background-color: #f8d24e !important;
    border-radius: 2rem !important;
  }

  ::-webkit-scrollbar-corner {
    background: #f8d24e !important;
  }
}

.modal-window__title--br {
  @media (max-width: 767px) {
    max-width: 142px;
  }

  color: #212121;
  font-family: Seymour One;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.4;
  letter-spacing: 0em;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}
.background-img {
  position: relative;
  height: 141px;
  margin-top: 15px;
  @media (min-width: 768px) {
    margin-top: 17px;
  }
}

.prod-img {
  width: 131px;
  height: 131px;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  position: absolute;

  left: 55px;
  z-index: 100;
  @media screen and (min-width: 768px) {
    left: 50%;
    transform: translateX(-50%);
  }
}

.modal-card {
  width: 132px;
  height: 131px;
  border-radius: 50%;
  position: absolute;
  left: 55px;
  z-index: 10;
  top: 7px;
  @media screen and (min-width: 768px) {
    left: 50%;
    transform: translateX(-50%);
  }

  background: linear-gradient(
      0deg,
      rgba(156, 190, 143, 0.7),
      rgba(156, 190, 143, 0.7)
    ),
    #d9d9d9;
  box-shadow: 0px 5.26027px 19.726px rgba(10, 136, 23, 0.25);
  border: none;
}

.modal__title {
  font-family: Montserrat;
  font-size: 20px;
  font-weight: 500;
  line-height: 28px;
  letter-spacing: 0em;
  text-align: center;
  color: #131313;
  margin-top: 39px;
  margin-bottom: 24px;
}

.ingr-list {
  color: #131313;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.7;
  letter-spacing: 0.04em;

  margin-bottom: 48px;
}

.ingr-item {
  display: flex;
  justify-content: space-between;
  &:not(:last-child) {
    margin-bottom: 11px;
  }
}
.component {
  display: flex;
  flex-direction: column;

  width: 53px;
  height: 72px;

  @media (min-width: 768px) {
    width: 65px;
    height: 90px;
  }

  border: 2px solid #9cbe8f;
  border-radius: 40px;
  align-items: center;
  justify-content: space-between;
}

.components {
  display: flex;
  gap: 9px;
  justify-content: center;
  margin-top: 15px;
}

.number {
  width: 43px;
  height: 43px;
  background: #9cbe8f;
  border: 2px solid #9cbe8f;
  box-shadow: 0px 6px 20px rgba(10, 136, 23, 0.25);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #131313;
  font-family: 'Montserrat';
  font-weight: 500;
  font-size: 10px;
  line-height: 2.8;
  font-style: normal;
  @media (min-width: 768px) {
    width: 53px;
    height: 53px;
  }
}

.unit {
  color: #131313;
  margin-bottom: 12px;
  font-family: 'Montserrat';
  font-weight: 400;
  font-size: 10px;
  line-height: 1.2;
  font-style: normal;
}

.form-field-ingr {
  display: flex;
  flex-direction: column;
  position: relative;
  margin-top: 16px;
}
.form-field-ing {
  @media (max-width: 768px) {
    max-width: 239px;
    margin: 0;
  }
}
.submit-button-ing {
  margin-bottom: 0;
}
